body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color: hsl(214, 15%, 91%);
}
a {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
  color: white;
}
li {
  list-style: none;
}
p {
  padding: 0;
  margin: 0;
}
span {
  padding: 0;
  margin: 0;
}
h3 {
  padding: 0;
  margin: 0;
}
.loadingContainer {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
      transform: translate(-50%, -50%);
}



